<template>
  <div>
    <b-button aria-controls="sidebar" aria-expanded=""
     @click="show=true" variant="info" class="text-dark" style="font-size:1.05em; padding-left: 0; padding-right: 0;">
      Category</b-button>
    <b-sidebar id="sidebar" v-model="show" shadow>
      <div class="px-3 py-2">
          <b-list-group-item v-for="(value,id) in Categories" :key="id" class="d-flex d-inline">
            <b-button id="button1" variant="light" :to="getCatLink(Categories[id].name)"
             class="my-auto text-center d-flex justify-content-between">{{Categories[id].name}}
            </b-button>
            <b-button variant="light" :aria-controls="'sidebar1'+id" @click="handler1(id)">
              <b-icon icon="chevron-right"></b-icon>
            </b-button>
          </b-list-group-item>
          <b-sidebar :id="'sidebar1'+id1" v-model="show1" no-header v-if="Categories && Categories[id1]">
            <template v-slot:default="{ hide }">
              <div class="px-3 py-2">
                <b-icon @click="hide" icon="arrow-left">Category</b-icon>
                <b-list-group-item v-for="(value,id) in Categories[id1].subCategories" :key="id" class="d-flex d-inline">
                    <b-button id="button2" variant="light" :to="getSubCatLink(Categories[id1].name,value)"
                    class="my-auto text-center d-flex justify-content-between">{{value}}
                    </b-button>
                    <!-- <b-button variant="light" :aria-controls="'sidebar2'+key" @click="handler2(id)">
                      <b-icon icon="chevron-right"></b-icon>
                    </b-button> -->
                </b-list-group-item>
              </div>
            </template>
          </b-sidebar>
      </div>
    </b-sidebar>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'ShowCategory',
  data () {
    return {
      show: false,
      show1: false,
      show2: false,
      id1: 0,
      id2: ''
      // Categories: {
      //   A: {
      //     B: ['C', 'D'],
      //     W: ['X', 'Y']
      //   },
      //   E: {
      //     F: ['G', 'H']
      //   }
      // }
    }
  },
  computed: {
    ...mapGetters({
      Categories: 'searchCategories'
    })
  },
  methods: {
    handler1 (key) {
      this.id1 = key
      this.show1 = true
    },
    getCatLink (item) {
      return {
        name: 'CertainCategory',
        query: {
          'cat-name': item
        }
      }
    },
    getSubCatLink (category, item) {
      return {
        name: 'CertainSubCategory',
        query: {
          'cat-name': category,
          'subcat-name': item
        }
      }
    }
  }
}
</script>

<style scoped>
#button1{
  width:80%
}
#button2{
  width:100%
}
</style>
