import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import(/* webpackChunkName: "Home" */ '../views/Home')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "Login" */ '../views/User/Entry/Login')
  },
  {
    path: '/signup',
    name: 'SignUp',
    component: () => import(/* webpackChunkName: "SignUp" */ '../views/User/Entry/SignUp')
  },
  {
    path: '/product/:id',
    name: 'CertainProduct',
    component: () => import(/* webpackChunkName: "CertainProduct" */ '../views/Product/CertainProduct')
  },
  {
    path: '/brands/all',
    name: 'AllBrands',
    component: () => import(/* webpackChunkName: "Brand" */ '../views/Brand/AllBrands')
  },
  {
    path: '/categories/all',
    name: 'AllCategories',
    component: () => import(/* webpackChunkName: "Category" */ '../views/Category/AllCategories')
  },
  {
    path: '/brand',
    name: 'CertainBrand',
    component: () => import(/* webpackChunkName: "Brand" */ '../views/Brand/CertainBrand')
  },
  {
    path: '/category',
    name: 'CertainCategory',
    component: () => import(/* webpackChunkName: "Category" */ '../views/Category/CertainCategory')
  },
  {
    path: '/sub-category',
    name: 'CertainSubCategory',
    component: () => import(/* webpackChunkName: "Category" */ '../views/Category/CertainSubCategory')
  },
  {
    path: '/flash-sale',
    name: 'FlashSale',
    component: () => import(/* webpackChunkName: "HomeSection" */ '../views/Product/FlashSale')
  },
  {
    path: '/most-sold',
    name: 'MostSold',
    component: () => import(/* webpackChunkName: "HomeSection" */ '../views/Product/MostSold')
  },
  {
    path: '/new-arrivals',
    name: 'NewArrivals',
    component: () => import(/* webpackChunkName: "HomeSection" */ '../views/Product/NewArrivals')
  },
  {
    path: '/our-shop',
    name: 'OurShop',
    component: () => import(/* webpackChunkName: "HomeSection" */ '../views/Product/OurShop')
  },
  {
    path: '/top-rated',
    name: 'TopRated',
    component: () => import(/* webpackChunkName: "HomeSection" */ '../views/Product/TopRated')
  },
  {
    path: '/search',
    name: 'SearchOnAll',
    component: () => import(/* webpackChunkName: "Search" */ '../views/Search/SearchOnAll')
  },
  {
    path: '/user',
    name: 'User',
    component: () => import(/* webpackChunkName: "User" */ '../views/User/UserNeeded'),
    children: [
      {
        path: '',
        name: 'UserProfile',
        component: () => import(/* webpackChunkName: "Account" */ '../views/User/Profile/UserProfile')
      },
      {
        path: 'edit',
        name: 'UserProfileEdit',
        component: () => import(/* webpackChunkName: "Account" */ '../views/User/Profile/UserProfileEdit')
      },
      {
        path: 'cart',
        name: 'CartDetails',
        component: () => import(/* webpackChunkName: "Order" */ '../views/User/Order/CartDetails')
      },
      {
        path: 'order/submit',
        name: 'OrderSubmitDetails',
        component: () => import(/* webpackChunkName: "Order" */ '../views/User/Order/OrderSubmitDetails')
      },
      {
        path: 'orders',
        name: 'OrderShower',
        component: () => import(/* webpackChunkName: "Order" */ '../views/User/Order/OrderShower')
      }
    ]
  },
  {
    path: '*',
    redirect: { name: 'Home' }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    }
    if (to.hash) {
      return { selector: to.hash }
    }
    return { x: 0, y: 0 }
  }
})

export default router
