<script>
import { mapActions, mapGetters } from 'vuex'
import SearchBar from './SearchBar'
import nested from '../components/ShowCategory'

export default {
  name: 'Header',
  data () {
    return {
      show1: false
    }
  },
  computed: {
    ...mapGetters([
      'searchBrand',
      'authToken',
      'user',
      'cartProductCount'
    ])
  },
  methods: {
    ...mapActions([
      'logOut'
    ]),
    getBrandLink (item) {
      return {
        name: 'CertainBrand',
        query: {
          name: item
        }
      }
    },
    handleLogOut () {
      this.logOut()
      const routeParent = this.$route.matched[0].name
      if (routeParent === 'User') {
        this.$router.push({ name: 'Home' })
      }
    }
  },
  components: {
    nested,
    SearchBar
  }
}
</script>
