<template>
  <div>
    <b-form @submit="search">
      <b-input-group class="mr-2">
        <!-- <b-input-group-prepend is-text>
          <b-icon size="lg" icon="search"></b-icon>
        </b-input-group-prepend> -->
        <b-form-input v-model="searchString" id="tar-1" placeholder="" autocomplete="off"></b-form-input>
        <b-button type="submit" @click="search" :disabled="searchString === ''">
          <b-icon size="lg" icon="search"></b-icon>
        </b-button>
      </b-input-group>
    </b-form>
    <b-popover :show.sync="show" target="tar-1" triggers="focus" placement="bottom">
      <b-list-group>
        <b-list-group-item v-for="(match, index) in matchedNames" :key="index" :to="getLink(match.item)" @click="show=false;searchString=''">
          <!-- <router-link :to="getLink(match.item)"> -->
          {{match.item.name}}
          <!-- </router-link> -->
      </b-list-group-item>
      </b-list-group>
    </b-popover>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Fuse from 'fuse.js'

export default {
  name: 'SearchBar',
  data () {
    return {
      searchString: '',
      show: false
    }
  },
  computed: {
    ...mapGetters([
      'searchProducts',
      'suggestionScope'
    ]),
    matchedNames () {
      if (this.searchString === '') {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.show = false
        return []
      }
      const fuse = new Fuse(this.suggestionScope, {
        keys: [
          'name',
          'category' // only subCategories will have 'category' property
        ]
      })
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.show = true
      return fuse.search(this.searchString).slice(0, process.env.VUE_APP_SEARCH_BAR_LIMIT)
    }
  },
  methods: {
    getLink (item) {
      if (item.type === 'brand') {
        return {
          name: 'CertainBrand',
          query: {
            name: item.name
          }
        }
      } else if (item.type === 'category') {
        return {
          name: 'CertainCategory',
          query: {
            'cat-name': item.name
          }
        }
      } else if (item.type === 'subCategory') {
        return {
          name: 'CertainSubCategory',
          query: {
            'cat-name': item.category,
            'subcat-name': item.name
          }
        }
      }
    },
    search (event) {
      event.preventDefault()
      const searchId = parseInt(this.searchString)
      if (this.searchProducts.find(product => product.id === searchId)) {
        this.$router.push({
          name: 'CertainProduct',
          params: {
            id: this.searchString
          }
        }).catch(() => {})
      } else {
        this.$router.push({
          name: 'SearchOnAll',
          query: {
            string: this.searchString
          }
        }).catch(() => {})
      }
    }
  }
}
</script>

