import api from '../../plugins/axios'

const user = {
  state: {
    authToken: null,
    user: {},
    userLoaded: false
  },
  getters: {
    authToken (state) {
      return state.authToken
    },
    user (state) {
      return state.user
    },
    userLoaded (state) {
      return state.userLoaded
    },
    getConfig (state) {
      return function (params, data) {
        return {
          params,
          data,
          headers: {
            Authorization: `Bearer ${state.authToken}`
          }
        }
      }
    }
  },
  mutations: {
    setAuthToken (state, token) {
      state.authToken = token
    },
    setUserProperty (state, properties) {
      // other choices:
      // 1. Object(state.user, properties) -> bad choice
      // 2. state.user = Object.assign(state.user, properties) -> bad choice
      // both are bad choices because they do not change the object reference
      // thus do not trigger reactive change (are not auto updated in the DOM by vue)
      // to fix this, create a completely new object reference then assign it to state.user
      state.user = Object.assign({}, state.user, properties)
      state.userLoaded = true
      sessionStorage.setItem('user', JSON.stringify(state.user))
    },
    clearUser (state) {
      state.user = {}
      state.userLoaded = false
    }
  },
  actions: {
    // takes in data: { phoneNo: '', password: '' } and makes signup request
    signUp (context, data) {
      return new Promise((resolve, reject) => {
        api.post('/signup', data)
          .then(response => {
            console.log('signup completed')
            console.log(response)
            context.commit('setAuthToken', response.data.token)
            localStorage.setItem('authToken', response.data.token)
            context.commit('setUserProperty', response.data.user)
            resolve(response)
          })
          .catch(error => {
            console.log('signup failed')
            console.log(error.response)
            reject(error)
          })
      })
    },

    // takes in data: { phoneNo: '', password: '' } and makes login request
    login (context, data) {
      return new Promise((resolve, reject) => {
        api.post('/login', data)
          .then(response => {
            console.log('login completed')
            console.log(response)
            context.commit('setAuthToken', response.data.token)
            localStorage.setItem('authToken', response.data.token)
            context.commit('setUserProperty', response.data.user)
            context.dispatch('loadCart').then()
            resolve(response)
          })
          .catch(error => {
            console.log('login failed')
            console.log(error.response)
            reject(error)
          })
      })
    },

    // takes in data: { phoneNo: '', oldPassword: '', newPassword: '' } and makes change-password request
    changePassword (context, data) {
      return new Promise((resolve, reject) => {
        api.patch('/change-password', data, context.getters.getConfig())
          .then(response => {
            console.log('password changed')
            console.log(response)
            resolve(response)
          })
          .catch(error => {
            console.log('password changed failed')
            console.log(error.response)
            reject(error)
          })
      })
    },

    // takes in data: { email: '', age: '', etc... } and makes change-info request
    changeInfo (context, data) {
      return new Promise((resolve, reject) => {
        api.patch('/change-info', data, context.getters.getConfig())
          .then(response => {
            console.log('info changed')
            console.log(response)
            context.commit('setUserProperty', data)
            resolve(response)
          })
          .catch(error => {
            console.log('info change failed')
            console.log(error.response)
            reject(error)
          })
      })
    },

    // loads user info from token saved in browser
    autoLogin (context) {
      const localStorageToken = localStorage.getItem('authToken')
      if (localStorageToken) {
        context.commit('setAuthToken', localStorageToken)
        let user = sessionStorage.getItem('user')
        if (user) {
          user = JSON.parse(user)
          context.commit('setUserProperty', user)
          context.dispatch('loadCart').then()
        } else {
          api.get('/auto-login', context.getters.getConfig())
            .then(response => {
              console.log('auto login completed')
              console.log(response)
              context.commit('setUserProperty', response.data)
              context.dispatch('loadCart').then()
            })
            .catch(error => {
              console.log('auto login failed')
              console.log(error.response)
            })
        }
      }
    },

    // logs the User out
    logOut (context) {
      context.commit('setAuthToken', null)
      context.commit('clearUser')
      context.commit('clearCart')
      localStorage.clear()

      const allNames = sessionStorage.getItem('allNames')
      sessionStorage.clear()
      sessionStorage.setItem('allNames', allNames)

      console.log('logout complete')
    }
  }
}

export default user
